import React, {Component} from 'react'

class Home extends Component{
    render(){
        return(
            <div className="home-container">
                <h3>有些事情，可以改变，我们就要尽力把它们往好的方面改变；有些事情，我们改变不了，但是我们可以坦然接受事实，至少我们不会心力交瘁。</h3>
            </div>
        )
    }
}

export default Home